'use client';

import { useState } from 'react';
import Link from 'next/link';
import BottomNavigation from '../components/BottomNavigation';

export default function SendPackagePage() {
    const [selectedType, setSelectedType] = useState<'within' | 'inter'>('within');
    const [pickupLocation, setPickupLocation] = useState('');
    const [dropOffLocation, setDropOffLocation] = useState('');

    return (
        <div className="min-h-screen bg-black text-white flex flex-col">
            {/* Status Bar */}
            <div className="flex justify-between items-center p-4 pt-12 text-sm">
                <span>9:41</span>
                <div className="flex items-center gap-1">
                    <div className="flex gap-1">
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                    </div>
                    <svg className="w-4 h-4" viewBox="0 0 24 24" fill="white">
                        <path d="M2 17h20v2H2v-2zm1.15-6.05L4 9.47l.85 1.48H5c.55 0 1 .45 1 1s-.45 1-1 1-.45-.45-.45-1zm5.5 2.5H9c.55 0 1-.45 1-1s-.45-1-1-1-.45.45-.45 1z" />
                    </svg>
                    <div className="w-6 h-3 border border-white rounded-sm">
                        <div className="w-4 h-1.5 bg-white rounded-sm m-0.5"></div>
                    </div>
                </div>
            </div>

            {/* Header */}
            <div className="text-center px-8 mb-8">
                <h1 className="text-4xl font-bold mb-2">Pickom</h1>
                <p className="text-gray-400 text-lg">People-Powered Delivery</p>

                <h2 className="text-3xl font-bold mt-12 mb-8">Send a Package</h2>

                {/* Delivery Type Selection */}
                <div className="flex gap-4 mb-8">
                    <button
                        onClick={() => setSelectedType('within')}
                        className={`flex-1 py-3 px-4 rounded-full text-sm font-medium transition-all ${selectedType === 'within'
                            ? 'bg-white text-black'
                            : 'bg-transparent text-white border border-gray-600'
                            }`}
                    >
                        Within City
                    </button>

                    <button
                        onClick={() => setSelectedType('inter')}
                        className={`flex-1 py-3 px-4 rounded-full text-sm font-medium transition-all ${selectedType === 'inter'
                            ? 'bg-white text-black'
                            : 'bg-transparent text-white border border-gray-600'
                            }`}
                    >
                        Inter-City
                    </button>
                </div>
            </div>

            {/* Form Fields */}
            <div className="flex-1 px-8 space-y-6 pb-20">
                <div>
                    <label className="block text-white text-lg font-medium mb-4">
                        Pickup Location
                    </label>
                    <input
                        type="text"
                        value={pickupLocation}
                        onChange={(e) => setPickupLocation(e.target.value)}
                        className="w-full bg-gray-800 border border-gray-600 rounded-lg px-4 py-4 text-white placeholder-gray-400 text-lg"
                        placeholder="Enter pickup address"
                    />
                </div>

                <div>
                    <label className="block text-white text-lg font-medium mb-4">
                        Drop-off Location
                    </label>
                    <input
                        type="text"
                        value={dropOffLocation}
                        onChange={(e) => setDropOffLocation(e.target.value)}
                        className="w-full bg-gray-800 border border-gray-600 rounded-lg px-4 py-4 text-white placeholder-gray-400 text-lg"
                        placeholder="Enter destination address"
                    />
                </div>

                {/* Continue Button */}
                <div className="pt-8">
                    <Link
                        href="/package-details"
                        className="w-full bg-gray-200 text-black py-4 rounded-full text-lg font-medium text-center block hover:bg-gray-300 transition-colors"
                    >
                        Continue
                    </Link>
                </div>
            </div>

            {/* Bottom Navigation */}
            <BottomNavigation />
        </div>
    );
} 